body {
  min-width: 320px;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 18px;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

.site-wrapper {
  position: relative;
}

.visually-hidden,
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
