.slider input[type="radio"] {
    display: none;
}

.slider__controls {
    margin: 0;
}

.slider__controls label {
    font-size: 0;
    display: inline-block;
    width: 6px;
    height: 6px;
    background-color: rgba(40, 54, 69, 0.3);
    border-radius: 50%;
    margin: 0 8px;
    cursor: pointer;
    transition: background-color 0.3s;
}

#btn-1:checked ~ .slider__controls label[for="btn-1"],
#btn-2:checked ~ .slider__controls label[for="btn-2"],
#btn-3:checked ~ .slider__controls label[for="btn-3"]{
    background-color: @second-color;
    border: 3px solid @primary-color;
}

#btn-4:checked ~ .slider__controls label[for="btn-4"],
#btn-5:checked ~ .slider__controls label[for="btn-5"],
#btn-6:checked ~ .slider__controls label[for="btn-6"]{
    background-color: @second-color;
    border: 3px solid @primary-color;
}

@media (max-width: @desktop-width) {
#btn-1:checked ~ .reviews__list .reviews__item:nth-child(1) {
    display: flex;
}

#btn-2:checked ~ .reviews__list .reviews__item:nth-child(2) {
    display: flex;
}

#btn-3:checked ~ .reviews__list .reviews__item:nth-child(3){
  display: flex;
}
}

@media (max-width: @tablet-width) {

#btn-4:checked ~ .tariff__wrapper .tariff__table {
    margin-left: 20px;
}

#btn-5:checked ~ .tariff__wrapper .tariff__table {
    margin-left: -254px;
}

#btn-6:checked ~ .tariff__wrapper .tariff__table {
    margin-left: -530px;
}

}
