.wrapper {
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .wrapper {
    width: 620px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .wrapper {
    width: 940px;
    margin: 0 auto;
  }
}
.page-header {
  display: flex;
  width: 100%;
  flex-direction: column;
  color: #ffffff;
  background-color: #283645;
  z-index: 1;
}
.page-header__logo {
  width: 320px;
  margin: 0 auto;
  border-bottom: 1px solid black;
  padding: 22px 0 25px;
}
.page-header__logo-link {
  display: block;
  width: 75px;
  height: 22px;
  margin-left: 20px;
}
.page-header__logo-link:focus svg {
  fill: #d22856;
}
.page-header__logo-link svg {
  fill: #ffffff;
}
.page-header__logo-link[href]:hover svg {
  fill: #d22856;
}
.page-header__logo-link[href]:active svg {
  fill: #ffffff;
  opacity: 0.3;
}
.page-header__logo-tablet {
  display: none;
}
.page-header__logo-desktop {
  display: none;
}
@media (min-width: 660px) {
  .page-header {
    z-index: 20;
  }
  .page-header__logo {
    padding: 27px 0 42px;
    width: 660px;
  }
  .page-header__logo-link {
    width: 147px;
    height: 40px;
  }
  .page-header__logo-mobile {
    display: none;
  }
  .page-header__logo-tablet {
    display: block;
  }
}
@media (min-width: 1200px) {
  .page-header {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .page-header__wrapper {
    width: 940px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    background: none;
  }
  .page-header__logo {
    border: none;
  }
  .page-header__logo-link {
    margin-left: 0;
  }
  .page-header__logo-tablet {
    display: none;
  }
  .page-header__logo-desktop {
    display: block;
  }
}
.main-nav {
  width: 320px;
  margin: 0 auto;
  position: relative;
  text-align: center;
}
.main-nav__toggle {
  position: absolute;
  top: -50px;
  right: 20px;
  padding: 0;
  background-color: transparent;
  border: none;
}
.main-nav__toggle svg {
  fill: #ffffff;
}
.main-nav__toggle:hover svg {
  fill: #d22856;
}
.main-nav__toggle:focus svg {
  fill: #d22856;
}
.main-nav__toggle:active svg {
  fill: rgba(210, 40, 86, 0.3);
}
@media (max-width: 1199px) {
  .main-nav--closed .page-header {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .main-nav--closed .page-header__logo {
    border-bottom: none;
  }
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
  .main-nav--closed .main-nav__toggle--open {
    display: block;
  }
  .main-nav--closed .main-nav__toggle--close {
    display: none;
  }
  .main-nav--opened .main-nav__toggle--open {
    display: none;
  }
  .main-nav--opened .main-nav__toggle--close {
    display: block;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__wrapper {
    display: block;
  }
  .main-nav--nojs .page-header {
    position: static;
    background-color: #283645;
  }
  .main-nav--nojs .page-header__logo {
    border-bottom: 1px solid black;
  }
}
@media (min-width: 660px) {
  .main-nav {
    width: 660px;
  }
  .main-nav__toggle {
    top: -65px;
  }
}
@media (min-width: 1200px) {
  .main-nav {
    width: 940px;
  }
  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
}
.site-list a {
  display: block;
  padding: 15px 0;
  color: #ffffff;
  font-size: 18px;
  line-height: 32px;
  font-weight: 700;
  text-transform: uppercase;
  border-bottom: 1px solid black;
  border-top: 1px solid rgba(255, 255, 255, 0.15);
}
.site-list a[href]:hover,
.site-list a[href]:focus {
  color: #d22856;
}
.site-list a[href]:active {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1200px) {
  .site-list {
    width: 625px;
    flex-direction: row;
    justify-content: space-between;
  }
  .site-list__item {
    border: none;
  }
  .site-list__item--active {
    position: relative;
  }
  .site-list__item--active::after {
    content: "";
    position: absolute;
    bottom: 35px;
    left: 0;
    right: 0;
    height: 2px;
    background: #ffffff;
  }
  .site-list a {
    padding: 46px 0;
    line-height: 18px;
    font-weight: normal;
    text-transform: none;
    border: none;
  }
}
.page-main {
  min-width: 320px;
